<template>
    <div>
        <input type="checkbox" id="nav-toggle" hidden>
	    <nav class="nav">
            <label for="nav-toggle" class="nav-toggle" onclick></label>
            <div class="user-info">
                <span class="data">{{date | date('datetime')}}</span>
                <img src="../assets/duck.png" alt="УУУТКАААА" width="40%" align="center" hspace="30%" vspace="10%" >
                <div class="input_par">
                    User:
                </div>
                <div class="input_par">
                    <button class="btn_logout" @click.prevent="logout">Logout</button>
                </div>
            </div>
        </nav>

         <div class="info_dev">
			<div class="info_dev_text">
				<p>The work was performed by Yuri Batalin and Egor Mitrofanov</p>
				Variant 81327
			</div>
		</div>

        <div class="cnv">
                <canvas id="canvas"></canvas>
        </div>

        <div class="input_param_side">
            <div class="input_side">
                <div class="input_par">
                    Input cord X:
                </div>
                <div class="input_par">
                    <input type="radio" id="r1" name="X" value="-4" onclick="draw()">-4
                    <input type="radio" id="r2" name="X" value="-3" onclick="draw()">-3
                    <input type="radio" id="r3" name="X" value="-2" onclick="draw()">-2
                    <input type="radio" id="r4" name="X" value="-1" onclick="draw()">-1
                    <input type="radio" id="r5" name="X" value="0" onclick="draw()" checked>0
                    <input type="radio" id="r1" name="X" value="1" onclick="draw()">1
                    <input type="radio" id="r2" name="X" value="2" onclick="draw()">2
                    <input type="radio" id="r3" name="X" value="3" onclick="draw()">3
                    <input type="radio" id="r4" name="X" value="4" onclick="draw()">4
                </div>
                <div class="input_par">
                    Input cord Y:
                </div>
                <div class="input_par">
                    <input class="input" type="text" name="y" id="y" placeholder="Input cord Y">
                </div>
                <div class="input_par">
                    Input R:
                </div>
                <div class="input_par">
                    <input type="radio" id="r1" name="R" value="-4" onclick="draw()">-4
                    <input type="radio" id="r2" name="R" value="-3" onclick="draw()">-3
                    <input type="radio" id="r3" name="R" value="-2" onclick="draw()">-2
                    <input type="radio" id="r4" name="R" value="-1" onclick="draw()">-1
                    <input type="radio" id="r5" name="R" value="0" onclick="draw()">0
                    <input type="radio" id="r1" name="R" value="1" onclick="draw()">1
                    <input type="radio" id="r2" name="R" value="2" onclick="draw()">2
                    <input type="radio" id="r3" name="R" value="3" onclick="draw()">3
                    <input type="radio" id="r4" name="R" value="4" onclick="draw()" checked>4
                </div>
                <div class="input_par">
                    <button class="btn_in_param">Input</button>
                </div>
            </div>
        </div>
        <div class="table-area">
			<div class="input_par">
				<table class="result-table">
					<thead>
						<td>X</td>
						<td>Y</td>
						<td>R</td>
						<td>RESULT</td>
					</thead>
					<tbody>
					</tbody>
				</table>
			</div>
		</div>
    </div>
</template>

<script>
export default {
    data: () => ({
        date: new Date(),
        interval: null
    }),
    methods: {
        logout() {
            console.log('Logout--->')
            this.$router.push('/login?message=logout')
        }
    },
    mounted() {
        this.interval = setInterval(() => {
            this.date = new Date()
        }, 1000)
    },
    beforeDestroy(){
        clearInterval(this.interval)
    }
}
</script>